import $ from '../core';
$.prototype.getAttribute = function (...atrName) {
	for (let i = 0; i < this.length; i++) {
		if (!this[i].getAttribute) {
			continue;
		}
		this[i].getAttribute(...atrName);
	}
	return this;
};
$.prototype.addAttribute = function (...attrNames) {
	for (let i = 0; i < this.length; i++) {
		if (!this[i].setAttribute) {
			continue;
		}
		this[i].setAttribute(...attrNames, '');
	}
	return this;
};

$.prototype.removeAttribute = function (...attrNames) {
	for (let i = 0; i < this.length; i++) {
		if (!this[i].removeAttribute) {
			continue;
		}
		this[i].removeAttribute(...attrNames);
	}
	return this;
};

$.prototype.toggleAttribute = function (attrNames) {
	for (let i = 0; i < this.length; i++) {
		if (!this[i].hasAttribute) {
			continue;
		}
		if (this[i].hasAttribute(attrNames)) {
			this[i].removeAttribute(attrNames);
		} else {
			this[i].setAttribute(attrNames, '');
		}
	}
	return this;
};
